export default{
	data(){
		return{
			yonghuming:'',
			mima:'',
			mima1:'',
			mima2:'',
			yonghuid:'',
		}	
	},
	onLoad:function(){
		var that=this;
		uni.getStorage({
		    key: 'storage_name',
		    success: function (res) {
		        console.log(res.data);
				that.yonghuming=res.data;
		    },
		});
		uni.getStorage({
		    key: 'storage_psw',
		    success: function (res) {
		        console.log(res.data);
				that.mima=res.data;
		    },
		});
		uni.getStorage({
		    key: 'storage_userCode',
		    success: function (res) {
		        console.log(res.data);
				that.yonghuid=res.data;
		    },
		});
		

	},
	methods:{
		wancheng:function(){
			var _self=this;
			
			uni.request({
				url:"http://192.168.1.143:8080/Users/updatePassByUserCode",
				method:'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded', 
				},
				data:{userCode:_self.yonghuid,passWord:_self.mima1},
				success:function(res){
					console.log(res);
					uni.reLaunch({
						url:"../login/login"
					})
				}
			})
			
		}
		}
	}
	
	
